.section.featured-slider {
    overflow: hidden;
}
.section.featured-slider .container {
    position: relative;
    padding: 0;
    max-width: none;
}
@media (min-width: $(desktop)) {
    .section.featured-slider .container {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-flex-flow: row wrap;
            -ms-flex-flow: row wrap;
                flex-flow: row wrap;
        max-width: 100%;
        margin-left: auto;
        margin-right: auto;
        padding: 100px 0;
    }
    @media (min-width: $(tablet)) {
        .section.featured-slider .container {
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            -webkit-flex-flow: row wrap;
                -ms-flex-flow: row wrap;
                    flex-flow: row wrap;
            max-width: 740px;
            margin-left: auto;
            margin-right: auto;
        }
    }
    @media (min-width: $(desktop)) {
        .section.featured-slider .container {
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            -webkit-flex-flow: row wrap;
                -ms-flex-flow: row wrap;
                    flex-flow: row wrap;
            max-width: 960px;
            margin-left: auto;
            margin-right: auto;
        }
    }
    @media (min-width: $desktop-large) {
        .section.featured-slider .container {
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            -webkit-flex-flow: row wrap;
                -ms-flex-flow: row wrap;
                    flex-flow: row wrap;
            max-width: 1160px;
            margin-left: auto;
            margin-right: auto;
        }
    }
}
.section.featured-slider .controls {
    display: none;
}
@media (min-width: $(desktop)) {
    .section.featured-slider .controls {
        display: block;
    }
}
.section.featured-slider .controls .previous,
        .section.featured-slider .controls .next {
    position: absolute;
    top: 50%;
    overflow: hidden;
    margin-top: -48px;
    z-index: 11;
}
.section.featured-slider .controls .previous a, .section.featured-slider .controls .next a {
    color: $white;
    font-size: 48px;
    display: inline-block;
    box-sizing: border-box;
    text-align: center;
    text-decoration: none;
    text-shadow: 1px 0 3px $black;
    -webkit-transition: all 250ms;
    transition: all 250ms;
}
.section.featured-slider .controls .previous a:hover, .section.featured-slider .controls .next a:hover {
    text-shadow: 0 0 5px $black;
}
.section.featured-slider .controls .previous a .icon, .section.featured-slider .controls .next a .icon {
    display: block;
    height: auto;
    width: auto;
    padding: 20px;
    margin: 0;
}
.section.featured-slider .controls .previous {
    left: 25px;
}
.section.featured-slider .controls .next {
    right: 25px;
}
.section.featured-slider .slider {
    position: relative;
    width: 100%;
}
.section.featured-slider .slides {
    width: 100%;
    position: relative;
}
.section.featured-slider .slides .slide {
    background-color: $blue;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    left: 100%;
    position: absolute;
    width: 100%;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
    overflow: hidden;
    padding: 20px;
    opacity: 0;
}
@media (min-width: $(desktop)) {
    .section.featured-slider .slides .slide {
        padding: 25px 50px;
    }
}
.section.featured-slider .slides .slide:nth-child(1) {
    left: 0;
    opacity: 1;
    position: relative;
}
@media (min-width: $(desktop)) {
    .section.featured-slider .slides .slide:nth-child(1) {
        position: absolute;
    }
}
.section.featured-slider .slides .slide:hover .background {
    -webkit-filter: blur(0) grayscale(0%) brightness(100%);
            filter: blur(0) grayscale(0%) brightness(100%);
}
.section.featured-slider .slides .slide:hover .color-overlay {
    opacity: 0;
}
.section.featured-slider .slides .slide:hover .text .title {
    text-shadow: 0 0 20px #000 !important;
}
.section.featured-slider .slides .slide:hover .subtitle span {
    background: $blue;
    box-shadow: 15px 0 0 $blue, -15px 0 0 $blue;
    text-shadow: none !important;
}
.section.featured-slider .slides .slide:hover .button {
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    box-shadow: 0 0 0 4px $white,
        inset 0 0 0 3px $blue;
    color: $blue;
}
.section.featured-slider .slides .slide .color-overlay {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: $blue_light;
    opacity: 0.6;
    -webkit-transition: opacity 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: opacity 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
.section.featured-slider .slides .slide .background {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-size: cover !important;
    background-position: center !important;
    -webkit-filter: blur(5px) grayscale(100%) brightness(40%);
            filter: blur(5px) grayscale(100%) brightness(40%);
    width: calc(100% + 20px);
    height: calc(100% + 20px);
    margin: -10px -10px;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform: translateZ(0) scale(1, 1);
            transform: translateZ(0) scale(1, 1);
    -webkit-transition: -webkit-filter 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: -webkit-filter 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: filter 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: filter 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955), -webkit-filter 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
.section.featured-slider .slides .slide a {
    text-decoration: none;
    display: block;
    with: 100%;
    color: $white;
}
.section.featured-slider .slides .slide a.full-width {
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
}
.section.featured-slider .slides .slide .text {
    width: 100%;
    color: $white;
}
.section.featured-slider .slides .slide .text .subtitle {
    font-family: 'Open Sans';
    text-transform: uppercase;
    margin-bottom: 20px;
}
@media (min-width: $(desktop)) {
    .section.featured-slider .slides .slide .text .subtitle {
        font-size: 32px;
    }
}
.section.featured-slider .slides .slide .text .subtitle span {
    padding: 10px 0;
    -webkit-transition: background 0.25s linear, box-shadow 0.25s linear;
    transition: background 0.25s linear, box-shadow 0.25s linear;
}
.section.featured-slider .slides .slide .text .title h2 {
    color: $white;
    display: block;
    font-size: 18px;
}
@media (min-width: $(desktop)) {
    .section.featured-slider .slides .slide .text .title h2 {
        font-size: 42px;
    }
}
.section.featured-slider .slides .slide .text .title,
                .section.featured-slider .slides .slide .text .subtitle span {
    letter-spacing: 3px;
    text-shadow: 1px 1px 0 #333;
    -webkit-transition: text-shadow 0.25s;
    transition: text-shadow 0.25s;
}
@media (min-width: $(desktop)) {
    .section.featured-slider .slides .slide .text .title,
                .section.featured-slider .slides .slide .text .subtitle span {
        margin-bottom: 20px;
    }
}
.section.featured-slider .slides .slide .text .button {
    background: $white;
    color: $blue;
    display: inline-block;
}
.section.featured-slider .slides .slide .overlay {
    width: 100%;
    text-align: center;
    position: relative;
    z-index: 3;
}
.section.featured-slider .slides .slide .overlay .icon {
    text-shadow: 2px 0 0 #333;
    display: block;
    margin: 0 auto;
    font-size: 48px;
    -webkit-transform: scale(0.65);
            transform: scale(0.65);
}
